import { Button, Form, Input, Typography, Divider, Tag, Select } from 'antd';
import React, { useRef, useState } from 'react';
import type { InputRef } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

import styles from './App.module.scss';

const { Title } = Typography;

const Tags = ({
  value = '',
  addBtnText,
  onChange
}: {
  value?: string;
  addBtnText?: string;
  onChange?: (data: string) => void;
}) => {
  const [inputVisible, setInputVisible] = useState(false);
  const inputRef = useRef<InputRef>(null);

  const arr = value.split(',').filter((item) => item);

  const handleInputConfirm = (e: any) => {
    setInputVisible(false);

    const val = e.target.value;

    if (typeof onChange === 'function' && val) {
      onChange(Array.from(new Set([...arr, e.target.value])).join(','));
    }
  };

  const showInput = () => {
    setInputVisible(true);
  };

  return (
    <>
      {arr?.map((tag) => {
        return (
          <Tag key={tag} closable>
            {tag}
          </Tag>
        );
      })}
      {inputVisible && (
        <Input
          size='small'
          className={styles['tag-input']}
          ref={inputRef}
          placeholder='UPUID'
          type='text'
          onBlur={handleInputConfirm}
          onPressEnter={handleInputConfirm}
        />
      )}
      {!inputVisible && (
        <Tag onClick={showInput}>
          <PlusOutlined /> {addBtnText}
        </Tag>
      )}
    </>
  );
};

const App: React.FC = () => {
  const [saveType, setSaveType] = useState('local');

  const onFinish = (values: any) => {
    console.log('Success:', values);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <div className={styles.app}>
      <div className={styles.hd}>
        <Title></Title>
      </div>
      <Divider />
      <Form
        name='basic'
        labelCol={{ span: 8 }}
        onValuesChange={({ saveType }) => {
          if (saveType) {
            setSaveType(saveType);
          }
        }}
        wrapperCol={{ span: 16 }}
        initialValues={{
          exclude: '',
          outputPath: '',
          include: '',
          keywords: '',
          ftpRemote: '',
          ftpPassword: '',
          ftpAccount: '',
          fileName: '{{title}}',
          saveType,
          duration: 300,
          expire: 600
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete='off'>
        <Form.Item label='' name='duration'>
          <Input type='number' placeholder='' />
        </Form.Item>

        <Form.Item label='' name='expire'>
          <Input type='number' placeholder='' />
        </Form.Item>

        <Form.Item
          label=''
          name='fileName'
          tooltip='{{title}}{{bvid}}{{cid}}'>
          <Input placeholder='' />
        </Form.Item>

        <Form.Item label='' name='saveType'>
          <Select
            options={[
              {
                value: 'local',
                label: ''
              },
              {
                value: 'ftp',
                label: 'FTP'
              }
            ]}
          />
        </Form.Item>

        {saveType === 'ftp' ? (
          <>
            <Form.Item label='FTP ' name='ftpRemote'>
              <Input type='text' placeholder='FTP' />
            </Form.Item>

            <Form.Item label='FTP ' name='ftpAccount'>
              <Input type='text' placeholder='FTP' />
            </Form.Item>

            <Form.Item label='FTP ' name='ftpPassword'>
              <Input type='text' placeholder='FTP' />
            </Form.Item>
          </>
        ) : null}

        <Form.Item
          label=''
          name='outputPath'
          tooltip='output/FTPFTP'>
          <Input placeholder='WindowsLinux' />
        </Form.Item>

        <Form.Item label='uid' name='exclude' tooltip='UP'>
          <Tags addBtnText='UPUID' />
        </Form.Item>

        <Form.Item
          label='uid'
          name='include'
          tooltip='UP'>
          <Tags addBtnText='UPUID' />
        </Form.Item>

        <Form.Item label='' name='keywords' tooltip=''>
          <Tags addBtnText='' />
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          <Button type='primary' htmlType='submit'>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default App;
